import React from "react";
import { Input, FormGroup, Col, Row } from "reactstrap";

const DiagnosticoDeSistemasAgrarios = ({
  inputDiagnosisOfAgriculturalSystems,
  setInputDiagnosisOfAgriculturalSystems,
}) => {
  const handleChangeInput = (event) => {
    const { name, value } = event.target;
    setInputDiagnosisOfAgriculturalSystems({
      ...inputDiagnosisOfAgriculturalSystems,
      [name]: value,
    });
  };

  return (
    <Row>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="name">
            Qual  a renda extra lote (fora do lote) anual?
          </label>
          <Input
            className="form-control-alternative"
            type="text"
            name="name"
            title="Qual  a renda extra lote (fora do lote) anual?"
            placeholder="Qual  a renda extra lote fora do lote) anual?"
            value={inputDiagnosisOfAgriculturalSystems.extraIncome}
            onChange={handleChangeInput}
            required
          />
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="name">
            A famlia recebe algum tipo de aux. de programa social
            governamental?
          </label>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            value={inputDiagnosisOfAgriculturalSystems.assistance}
            title="A famlia recebe algum tipo de aux. de programa social
          governamental?"
            name="assistance"
            id="select"
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="name">
            A famlia acessou alguma poltica pblica para moradia?
          </label>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            value={inputDiagnosisOfAgriculturalSystems.publicHousingpolicy}
            title="A famlia acessou alguma poltica pblica para moradia?"
            name="publicHousingpolicy"
            id="select"
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="name">
            A famlia acessou alguma linha de financiamento para projetos
            desenvolvidos no lote?
          </label>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            value={inputDiagnosisOfAgriculturalSystems.financing}
            title="A famlia acessou alguma linha de financiamento para projetos desenvolvidos no lote?"
            name="financing"
            id="select"
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="name">
            Tem meio de comunicao rural?
          </label>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            value={inputDiagnosisOfAgriculturalSystems.ruralCommunicationMedium}
            title="Tem meio de comunicao rural?"
            name="ruralCommunicationMedium"
            id="select"
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="name">
            Comunicao rural - Tipo
          </label>
          <Input
            className="form-control-alternative"
            type="text"
            name="ruralCommunicationType"
            value={inputDiagnosisOfAgriculturalSystems.ruralCommunicationType}
            placeholder="Comunicao rural - Tipo"
            onChange={handleChangeInput}
            title="Comunicao rural - Tipo"
            required
          />
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="name">
            Destino final do lixo seco
          </label>
          <Input
            className="form-control-alternative"
            type="text"
            name="garbageDestination"
            title="Destino final do lixo seco"
            placeholder="Destino final do lixo seco"
            value={inputDiagnosisOfAgriculturalSystems.garbageDestination}
            onChange={handleChangeInput}
            required
          />
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="name">
            Saneamento bsico da moradia (esgoto sanitrio)
          </label>
          <Input
            className="form-control-alternative"
            type="text"
            name="sanitation"
            title="Saneamento bsico da moradia (esgoto sanitrio)"
            placeholder="Saneamento bsico da moradia (esgoto sanitrio)"
            value={inputDiagnosisOfAgriculturalSystems.sanitation}
            onChange={handleChangeInput}
            required
          />
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="name">
            Principal meio de transporte escolar
          </label>
          <Input
            className="form-control-alternative"
            type="text"
            name="schoolTransport"
            title="Principal meio de transporte escolar"
            placeholder="Principal meio de transporte escolar"
            value={inputDiagnosisOfAgriculturalSystems.schoolTransport}
            onChange={handleChangeInput}
            required
          />
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="name">
            Contrata empregados para ajudar na produo do lote?
          </label>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            value={inputDiagnosisOfAgriculturalSystems.hireEmployees}
            title="Contrata empregados para ajudar na produo do lote?"
            name="hireEmployees"
            id="select"
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="name">
            Quantos empregados permanentes
          </label>
          <Input
            className="form-control-alternative"
            type="number"
            name="howManyPermanentEmployees"
            title="Quantos empregados permanentes"
            placeholder="Quantos empregados permanentes"
            value={
              inputDiagnosisOfAgriculturalSystems.howManyPermanentEmployees
            }
            onChange={handleChangeInput}
            required
          />
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="name">
            Recebeu assistncia tcnica na ltima safra/ano?
          </label>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            value={
              inputDiagnosisOfAgriculturalSystems.receivedTechnicalAssistance
            }
            title="Recebeu assistncia tcnica na ltima safra/ano?"
            name="receivedTechnicalAssistance"
            id="select"
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="name">
            Lembra da ltima queimada descontrolada que atingiu o lote?
          </label>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            value={inputDiagnosisOfAgriculturalSystems.rememberTheLastFire}
            title="Lembra da ltima queimada descontrolada que atingiu o lote?"
            name="rememberTheLastFire"
            id="select"
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="name">
            Ano dessa queimada
          </label>
          <Input
            className="form-control-alternative"
            type="text"
            name="yearOfTheBurn"
            title="Ano dessa queimada"
            placeholder="Ano dessa queimada"
            value={inputDiagnosisOfAgriculturalSystems.yearOfTheBurn}
            onChange={handleChangeInput}
            required
          />
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="name">
            O lote tem acesso a gua?
          </label>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            value={inputDiagnosisOfAgriculturalSystems.accessToWater}
            title="O lote tem acesso a gua?"
            name="accessToWater"
            id="select"
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="name">
            Tem eletrificao?
          </label>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            value={inputDiagnosisOfAgriculturalSystems.electricity}
            title="Tem eletrificao?"
            name="electricity"
            id="select"
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="name">
            Tipo de rede
          </label>
          <Input
            className="form-control-alternative"
            type="text"
            name="NetworkType"
            title="Tipo de rede"
            placeholder="Tipo de rede"
            value={inputDiagnosisOfAgriculturalSystems.NetworkType}
            onChange={handleChangeInput}
            required
          />
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="name">
            Energia disponvel atender  produo?
          </label>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            value={
              inputDiagnosisOfAgriculturalSystems.availableEnergyMeetsProduction
            }
            title="Energia disponvel atender  produo?"
            name="availableEnergyMeetsProduction"
            id="select"
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="name">
            Complementa o fornecimento de energia?
          </label>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            value={inputDiagnosisOfAgriculturalSystems.complementsEnergySupply}
            title="Complementa o fornecimento de energia?"
            name="complementsEnergySupply"
            id="select"
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="name">
            Utiliza, tambm, algum dessses?
          </label>
          <Input
            className="form-control-alternative"
            type="text"
            name="itAlsoUses"
            title="Utiliza, tambm, algum dessses?"
            placeholder="Utiliza, tambm, algum dessses?"
            value={inputDiagnosisOfAgriculturalSystems.itAlsoUses}
            onChange={handleChangeInput}
            required
          />
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="name">
            Deseja obter/complementar?
          </label>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            value={inputDiagnosisOfAgriculturalSystems.wantToGetComplementary}
            title="Deseja obter/complementar?"
            name="wantToGetComplementary"
            id="select"
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="name">
            Tratamento da gua de consumo
          </label>
          <Input
            className="form-control-alternative"
            type="text"
            name="waterTreatment"
            title="Tratamento da gua de consumo"
            placeholder="Tratamento da gua de consumo"
            value={inputDiagnosisOfAgriculturalSystems.waterTreatment}
            onChange={handleChangeInput}
            required
          />
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="name">
            Lote tem acesso a gua o ano todo?
          </label>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            value={inputDiagnosisOfAgriculturalSystems.accessToWaterAllYear}
            title="Lote tem acesso a gua o ano todo?"
            name="accessToWaterAllYear"
            id="select"
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="name">
            Fontes de gua natural no lote so protegidas por mata ciliar?
          </label>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            value={
              inputDiagnosisOfAgriculturalSystems.protectedByRiparianForest
            }
            title="Fontes de gua natural no lote so protegidas por mata ciliar?"
            name="protectedByRiparianForest"
            id="select"
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="name">
            Distncia entre local da captao da fonte de gua natural para
            moradia e elementos contaminantes? (m)
          </label>
          <Input
            className="form-control-alternative"
            type="number"
            name="distanceFromTheCatchment"
            title="Distncia entre local da captao da fonte de gua natural para moradia e elementos contaminantes? (m)"
            placeholder="Ex: 50"
            value={inputDiagnosisOfAgriculturalSystems.distanceFromTheCatchment}
            onChange={handleChangeInput}
            required
          />
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="name">
            A captao levou  alterao no curso natural do crrego da fonte?
          </label>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            value={inputDiagnosisOfAgriculturalSystems.alterationWatercourse}
            title="A captao levou  alterao no curso natural do crrego da fonte?"
            name="alterationWatercourse"
            id="select"
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="name">
            H captao de gua da chuva para reaproveitamento no lote?
          </label>
          <Input
            className="form-control-alternative"
            type="select"
            onChange={handleChangeInput}
            value={inputDiagnosisOfAgriculturalSystems.rainwaterHarvesting}
            title="H captao de gua da chuva para reaproveitamento no lote?"
            name="rainwaterHarvesting"
            id="select"
            required
          >
            <option value={undefined} hidden>
              Escolha uma opo
            </option>
            <option value={true}>Sim</option>
            <option value={false}>No</option>
          </Input>
        </FormGroup>
      </Col>
      <Col lg="6">
        <FormGroup>
          <label className="form-control-label" htmlFor="name">
            Como  utilizada a gua captada da chuva?
          </label>
          <Input
            className="form-control-alternative"
            type="text"
            name="howRainwaterIsUsed"
            title="Como  utilizada a gua captada da chuva?"
            placeholder="Como  utilizada a gua captada da chuva?"
            value={inputDiagnosisOfAgriculturalSystems.howRainwaterIsUsed}
            onChange={handleChangeInput}
            required
          />
        </FormGroup>
      </Col>
    </Row>
  );
};

export default DiagnosticoDeSistemasAgrarios;
