import React from "react";
import { Input, InputGroup } from "reactstrap";

function Documentacao({ inputDocumentation, setInputDocumentation }) {
  const handleChangeInput = (event) => {
    const { name, value } = event.target;
    setInputDocumentation({ ...inputDocumentation, [name]: value });
  };

  return (
    <div className="row">
      <div className="col-12">
        <h3>Informaes Gerais</h3>
        <hr />
      </div>
      <InputGroup className="mb-3 col-xl-6 col-sm-12 col-lg-6">
        <Input
          type="text"
          name="documentsArea"
          title="rea na Documentao (ha)"
          placeholder="rea na Documentao (ha)"
          value={inputDocumentation.documentsArea}
          onChange={handleChangeInput}
          required
        />
      </InputGroup>
      <InputGroup className="mb-3 col-xl-6 col-sm-12 col-lg-6">
        <Input
          type="text"
          name="propertyName"
          title="Nome da Propriedade"
          placeholder="Nome da Propriedade"
          value={inputDocumentation.propertyName}
          onChange={handleChangeInput}
          required
        />
      </InputGroup>
      <InputGroup className="mb-3 col-xl-6 col-sm-12 col-lg-6">
        <Input
          custom
          type="select"
          onChange={handleChangeInput}
          value={inputDocumentation.georeferencing}
          title="Georreferenciada"
          name="georeferencing"
          id="select"
          required
        >
          <option value={undefined} hidden>
            Georreferenciada
          </option>
          <option value={true}>Sim</option>
          <option value={false}>No</option>
        </Input>
      </InputGroup>
      <InputGroup className="mb-3 col-xl-6 col-sm-12 col-lg-6">
        <Input
          type="text"
          name="ownership"
          title="Propriedade / Posse / Concesso"
          placeholder="Propriedade / Posse / Concesso"
          value={inputDocumentation.ownership}
          onChange={handleChangeInput}
          required
        />
      </InputGroup>
      <div className="col-12">
        <h3>Documentao - Propriedade - Somente para Propriedade</h3>
        <hr />
      </div>
      <div className="col-12">
        <h3>Documentao - Concesso - Tipo de Documento</h3>
        <hr />
      </div>
      <InputGroup className="mb-3 col-xl-6 col-sm-12 col-lg-6">
        <Input
          custom
          type="select"
          onChange={handleChangeInput}
          value={inputDocumentation.domainTitle}
          title="Ttulo de Domnio"
          name="domainTitle"
          id="select"
          required
        >
          <option value={undefined} hidden>
            Ttulo de Domnio
          </option>
          <option value={true}>Sim</option>
          <option value={false}>No</option>
        </Input>
      </InputGroup>
      <InputGroup className="mb-3 col-xl-6 col-sm-12 col-lg-12">
        <Input
          custom
          type="select"
          onChange={handleChangeInput}
          value={inputDocumentation.ccu}
          title="Contrato de Concesso de Uso - CCU"
          name="ccu"
          id="select"
          required
        >
          <option value={undefined} hidden>
            Contrato de Concesso de Uso - CCU
          </option>
          <option value={true}>Sim</option>
          <option value={false}>No</option>
        </Input>
      </InputGroup>
      <InputGroup className="mb-3 col-xl-6 col-sm-12 col-lg-12">
        <Input
          custom
          type="select"
          onChange={handleChangeInput}
          value={inputDocumentation.ccdru}
          title="Contrato de Concesso de Direito Real de Uso - CCDRU"
          name="ccdru"
          id="select"
          required
        >
          <option value={undefined} hidden>
            Contrato de Concesso de Direito Real de Uso - CCDRU
          </option>
          <option value={true}>Sim</option>
          <option value={false}>No</option>
        </Input>
      </InputGroup>
      <InputGroup className="mb-3 col-xl-6 col-sm-12 col-lg-6">
        <Input
          custom
          type="select"
          onChange={handleChangeInput}
          value={inputDocumentation.regularization}
          title="Em Regularizao"
          name="regularization"
          id="select"
          required
        >
          <option value={undefined} hidden>
            Em Regularizao
          </option>
          <option value={true}>Sim</option>
          <option value={false}>No</option>
        </Input>
      </InputGroup>
      <div className="col-12">
        <h3>Documentao - Propriedades e Concesses</h3>
        <hr />
      </div>
      <InputGroup className="mb-3 col-xl-6 col-sm-12 col-lg-6">
        <Input
          type="text"
          name="sncr"
          title="Cdigo de Sistema Nacional de Cadastro Rural (SNCR)"
          placeholder="Cdigo de Sistema Nacional de Cadastro Rural (SNCR)"
          value={inputDocumentation.sncr}
          onChange={handleChangeInput}
          required
        />
      </InputGroup>
      <InputGroup className="mb-3 col-xl-6 col-sm-12 col-lg-6">
        <Input
          type="text"
          name="certification"
          title="Certificao do Imvel no INCRA"
          placeholder="Certificao do Imvel no INCRA"
          value={inputDocumentation.certification}
          onChange={handleChangeInput}
          required
        />
      </InputGroup>
    </div>
  );
}

export default Documentacao;